import {Component} from '@angular/core';
import {IonicPage, NavController, NavParams} from 'ionic-angular';


@IonicPage()
@Component({
  selector: 'page-remittance-info01',
  templateUrl: 'remittance-info01.html',
})
export class RemittanceInfo01Page {
  exchangeHouseList;
  filterData = [];
  searchTerm;
  pinNo;

  constructor(public navCtrl: NavController,
              public navParams: NavParams) {

    this.exchangeHouseList = this.navParams.get("exchangeHouseList");
    this.pinNo=this.navParams.get("pinNo");
    this.exchangeHouseList = JSON.parse(this.exchangeHouseList);
    this.filterData = this.exchangeHouseList;
  }

  setFilteredItems = function () {
    this.filterData = this.exchangeHouseList.filter((exHouse) => {
      return ((exHouse.ID.toLowerCase().indexOf(this.searchTerm.toLowerCase()) > -1) ||
        (exHouse.Title.toLowerCase().indexOf(this.searchTerm.toLowerCase()) > -1));
    });
  };

  clickSingleExHouse = function (selectedExchangeHouse) {
    this.navCtrl.popToRoot({animate: false});
    this.navCtrl.push('RemittanceInfo02Page', {
      'selectedExchangeHouse': JSON.stringify(selectedExchangeHouse), 'navType': "2",
      'exchangeHouseList': JSON.stringify(this.exchangeHouseList),
      'pinNo': this.pinNo
    });
  };


  ionViewDidLoad() {
  }

}



